<template lang="html">
  <div class="row">
    <div class="col-lg-8 col-md-8 col-sm-12">
      <form-card
        v-for="group of centerGroups"
        :group="group"
        v-model="form"
        @updateHandle="updateHandle"
      ></form-card>
      <custom-field
        v-if="customField.length > 0"
        :group="customField"
        :value="custom_field"
        v-model="custom_field"
      ></custom-field>
      <div class="card">
        <div class="card-header">
          <h6 class="title">Danh sách sản phẩm</h6>
        </div>
        <div class="card-body form-card p-0">
          <my-table :columnDefs="columnDefs" v-bind:data-rows="productInCollection" :actions="actions" :actionsTable="[]"/>
        </div>
      </div>
    </div>
    <div class="col-lg-4 col-md-4 col-sm-12">
      <form-card
        v-for="group of sideGroups"
        hasTop="true"
        :group="group"
        v-model="form"
      >
      </form-card>
      <div class="card" v-if="listView">
        <div class="card-header">
          <h6 class="title">Giao diện</h6>
        </div>
        <div class="card-body form-card">
          <div class="row">
            <div class="col-sm-12">
              <my-select
                class="full-width"
                :attribute="listView"
                placeholder="Giao diện"
                v-model="form.template"
              >
              </my-select>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="container-fluid">
      <div class="page-action">
        <el-button @click="cancel">Hủy</el-button>
        <el-button type="primary" @click="save">Cập nhật</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import FormCard from 'src/components/UIComponents/FormCard.vue'
import CustomField from 'src/components/UIComponents/CustomField.vue'
import { Message, MessageBox, Input, Button } from 'element-ui';
import MySelect from 'src/components/UIComponents/Select'
import MyTable from 'src/components/UIComponents/Table.vue'
import dataFrom from './collection-form'
import { mapState } from 'vuex'
import util from '@/helpers/util'
import dtHelper from 'src/helpers/datatable'
import productSchemas from './product-collection-schemas'

let _form = {}, _custom_field = {}, firstGroups, secondGroups;

export default {
  components: {
    FormCard,
    ElInput: Input,
    ElButton: Button,
    MySelect,
    CustomField,
    MyTable
  },
  beforeCreate() {
    firstGroups = dataFrom[0].groups;
    secondGroups = dataFrom[1].groups;

    if (this.$store.state.customFieldDefine.collection){
      _custom_field = this.$store.state.customFieldDefine.collection.reduce(function (result, item) {
        result[item.handle] = '';
        return result;
      }, {});
    }

    firstGroups.forEach(group => {
      group.attributes.forEach(attr => {
        if (attr.multiple && _form[attr.prop]) {
          _form[attr.prop] = [];
        } else {
          _form[attr.prop] = '';
        }
        attr.value = _form[attr.prop];
      })
    })
    secondGroups.forEach(group => {
      group.attributes.forEach(attr => {
        if (attr.multiple && _form[attr.prop]) {
          _form[attr.prop] = [];
        } else {
          _form[attr.prop] = '';
        }
        attr.value = _form[attr.prop];
      })
    })

    let id = this.$route.params.id;
    this.$store.dispatch('getCustomField', {
      type: 'collection',
      id: id
    });
    this.$store.dispatch('getListThemeView', 'collection');
  },
  data () {
    return {
      centerGroups: firstGroups,
      sideGroups: secondGroups,
      custom_field: _custom_field,
      show_fields: [],
      actions: [
        {
          type: 'danger',
          icon: 'nc-icon nc-simple-remove',
          callback: this.removeProduct
        },
      ],
      columnDefs: dtHelper.buildColumDefs(productSchemas),
    }
  },
  computed: {
    ...mapState({
      originalForm: state => state.collectionDetail,
      productInCollection: state => state.productInCollection,
    }),
    form: {
      get () {
        let hashSeo = {};
        if (this.originalForm.seo){
          hashSeo = util.hashFlatten({seo: this.originalForm.seo});
        }
        let _originalForm = Object.assign({}, this.originalForm, hashSeo)
        return _originalForm;
      },
      set (value) {}
    },
    customField: {
      get() {
        let data = this.$util.filterByHandle(this.$store.state.customField, this.show_fields);
        for (let item of data) {
          if (item.value) {
            _custom_field[item.handle] = item.value;
          } else {
            _custom_field[item.handle] = '';
          }
        }
        return data||'';
      },
      set(value) {}
    },
    listView() {
      let self = this;
      let listTheme = self.$store.state.listThemeView;
      if (listTheme.length) {
        listTheme.unshift({
          title: 'Mặc định',
          value: ''
        })
        return {
          options: listTheme
        };
      }
      return '';
    }
  },
  mounted() {
    this.getContext();
    let id = this.$route.params.id;
    this.$store.dispatch('fetchCollectionDetail', id);

    this.$store.dispatch('setPageTitle', 'Cập nhật nhóm sản phẩm');
    this.$store.dispatch('setCurrentActions', [{
      label: 'Xem trên web',
      type: 'default',
      icon: '',
      callback: this.view
    }, {
      label: 'Xóa',
      type: 'warning',
      icon: '',
      callback: this.remove
    }, {
      label: 'Cập nhật',
      type: 'primary',
      icon: '',
      callback: this.save
    }])
  },
  methods: {
    removeProduct(index, row){
      let self = this;
      MessageBox.confirm('Bạn có chắc chắn xóa khỏi nhóm sản phẩm không?', 'Warning', {
        confirmButtonText: 'Đồng ý',
        cancelButtonText: 'Hủy bỏ',
        type: 'warning',
        center: true
      }).then(() => {
        this.$store.dispatch('removeProductToCollection', {
          arrIdCollection: [self.form.id],
          arrId: [row.id]
        }).then(res => {
          Message({
            type: 'success',
            message: 'Đã xóa sản phẩm khỏi nhóm sản phẩm'
          });
        });
      }).catch(() => {
        Message({
          type: 'info',
          message: 'Hủy bỏ'
        });
      })
    },
    changeTreeProduct(node, targetTree, oldTree){
      let self = this
      let products = [];
      let data = targetTree.data;
      let rootID = this.form.id;
      data.forEach(function (element, index) {
        let temp = {}
        temp.id = element.id;
        temp.parent_id = rootID;
        products.push(temp);
        self.getDataUpdateProduct(element.children, products);
      })
      this.$store.dispatch('', {products: products}).then(result => {
        Message({
          type: 'success',
          message: 'Cập nhật thành công'
        });
      }).catch(error => {
        Message({
          type: 'warning',
          message: 'Có lỗi xảy ra'
        });
      })
    },
    getDataUpdateProduct(children, menus){
      let self = this
      if (children.length){
        children.forEach(function (element, index) {
          let temp = {};
          temp.id = element.id;
          temp.parent_id = element.parent.id;
          menus.push(temp);
          self.getDataUpdateProduct(element.children, menus);
        })
      }
    },
    cancel() {
      this.$router.push({name: 'AllCollections'});
    },
    remove(){
      let self = this;
      MessageBox.confirm('Bạn có chắc chắn xóa không?', 'Warning', {
        confirmButtonText: 'Đồng ý',
        cancelButtonText: 'Hủy bỏ',
        type: 'warning',
        center: true
      }).then(() => {
        self.$util.updateStatusAll('collection', [self.form], 'delete').then(function (result) {
          self.$store.dispatch('fetchCollections');
          Message({
            type: 'success',
            message: 'Xóa thành công'
          });
          self.$router.push({name: 'AllCollections'});
        }).catch(function (error) {
          Message({
            type: 'error',
            message: error.message
          });
        });
      })
    },
    view(){
      let self = this;
      var win = window.open(self.form.url, '_blank');
      if (win) {
        win.focus();
      } else {
        Message({
          message: 'Please allow popups for this website',
          type: 'error'
        });
      }
    },
    updateHandle(){
      let self = this;
      let data = {
        handle: self.$util.createHandle(self.form.title),
        lang: 'vi'
      }
      self.$store.dispatch('checkHandle', data).then(result => {
        self.$store.state.collectionDetail = self.form;
        self.$store.state.collectionDetail.handle = result;
      });
    },
    save() {
      let self = this;
      this.$validator.validateAll().then(function (result) {
        if (result) {
          if (self.form.parent) {
            self.form.parent_id = self.form.parent.id;
          }
          self.$store.dispatch('updateCollection', self.form).then(res => {
            self.$store.dispatch('storeCustomField', self.$util.parseDataCustomfield(self.customField, self.custom_field, 'collection', self.form.id));

            Message({
              message: 'Cập nhật thành công',
              type: 'success'
            });
          }, res => {
            Message({
              message: res.message,
              type: 'error'
            });
          });
        }
      });
    },
    getContext() {
      let ctx = this.$util.getContext(this.form, {module: 'collections'});
      this.$store.state.ctx = ctx;
      return ctx;
    },
  },
  watch: {
    'form': {
      handler(newVal, oldVal) {
        let ctx = this.getContext();
        let output = window.__FUNC.CustomField(ctx);
        this.show_fields = output;
      },
      deep: true
    }
  },
  destroyed(){
    this.$store.dispatch('setCurrentActions', []);
    this.$store.state.collectionDetail = [];
    this.$store.state.listThemeView = [];
    this.$store.state.customField = [];
  }
}
</script>
